  class UVaFlickrAlbum extends HTMLElement {
  
    static get is() {
      return 'uva-flickr-album';
    }
  
    constructor(...args) {
      // Constructor
      try {
        const self = super(...args);
      } catch(e) {
        // Si no somos capaces de cargar al padre, error
        throw "Error al crear el elemento UVaFlickrAlbum " + e;
      } finally {
        //Variables
  
        // Creamos el shadow del elemento
        let shadowRoot = this.attachShadow({ mode: 'open' });
        this.shadowRoot.innerHTML = `
        <style>
        div {
          display: block;
        }
        img {
          position: relative;
          float: left;
          width:  100px;
          height: 100px;
          background-position: 50% 50%;
          background-repeat:   no-repeat;
          background-size:     cover;
        }
        </style>
        <div>
        <p id="title" style="display:none">TITLE</p>
        <img id="photo" />
        </div>
        `;
      }
    }
  
    connectedCallback() {
      //Parmametros
    }
  
    disconnectedCallback() {
    //	this.removeEventListener('click', this._onclick);
    }
  
    adoptedCallback() {  
  
    }
  
    attributeChangedCallback(name, oldValue, newValue) { 
      
    }
    get data() {
      return this._data;
    }
    set data(val) {
      console.log("--> UVaFlickrAlbum set Data")
      //Modificamos los datos
      this._data=val;
      this._render();
    }
  
    _render() {
      console.log("--> UVaFlickrAlbum _render");
      console.log(this._data);
      if (this._data.photo) {
        this.shadowRoot.getElementById("photo").setAttribute("src",this.data.photo);
        this.shadowRoot.getElementById("photo").setAttribute("class","img-responsive");
      }
      if (this._data.title) {
        this.shadowRoot.getElementById("title").innerText=this._data.title;
        this.shadowRoot.getElementById("title").style.display="";
      }
    }
  
  }
  customElements.define(UVaFlickrAlbum.is, UVaFlickrAlbum);

