'use strict';
let uvaNavegacionCounter = 0;

function handleLoad(e) {
  console.warn('*** Loaded import: ' + e.target.href);
}
function handleError(e) {
  console.error('*** Error loading import: ' + e.target.href);
}

class UVaNavegacion extends HTMLElement {
  
  static get is() {
    return 'uva-navegacion';
  }

  constructor(...args) {
    // Constructor
    try {
      const self = super(...args);
    } catch(e) {
	  // Si no somos capaces de cargar al padre, error
      throw "Error al crear el elemento UVaNavegacion " + e;
    } finally {
      //Variables
      if (!this.id)
         this.id = this.is+`-${uvaNavegacionCounter++}`;
      this._onlyone=false;
      this._uridata=undefined;
      this._data=undefined;
      this._localedata="es";
      this._data_locale=undefined;
      this._startLevel=0;
      this._levels=99;

      console.log(document.documentElement.lang);
      //Funciones
      this._find_element = this._find_element.bind(this);
      this._onclick = this._onclick.bind(this);
      // Creamos el shadow del elemento
      let shadowRoot = this.attachShadow({ mode: 'open' });
      this.shadowRoot.innerHTML = `
      <style>
      //@import url('https://stackpath.bootstrapcdn.com/bootstrap/4.1.1/css/bootstrap.min.css');
      //@import url('https://use.fontawesome.com/releases/v5.5.0/css/all.css');
     
      </style>
      <p>HOLA NAVEGACION</p>
      <div id="menu_primary" ></div>
      <div id="menu_secondary"></div>
      `;
    }
  }

  connectedCallback() {
    //Parmametros
    if (this.getAttribute("onlyone"))
      this._onlyone=true;
    if (this.getAttribute("data")) {
      this._uridata=this.getAttribute("data");
    } else {
      this._uridata="/ws/menu.json"
    }
    if (this.getAttribute("uri"))
      this._uri = this.getAttribute('uri');
    if (this.getAttribute("startLevel"))
      this._startLevel = parseInt(this.getAttribute('startLevel'));
    if (this.getAttribute("levels"))
      this._levels = this.getAttribute('levels');
    if (this.getAttribute("locale"))
      this._localedata = this.getAttribute('locale');

    this.addEventListener('click', this._onclick);
    
    //fetch(this._uridata)
    //.then(response=>response.json())
    loadJSONComunicacion(this._uridata)
    .then(response => {
      this.data=response;
    });
  }

  disconnectedCallback() {
  	this.removeEventListener('click', this._onclick);
  }

  adoptedCallback() {  

  }
  
  static get observedAttributes() {
    return ['locale'];
  }

  attributeChangedCallback(name, oldValue, newValue) { 
    console.log("--> attributeChangedCallback");
    console.log(name);
    console.log(oldValue);
    console.log(newValue);

    if (name==="locale") {
      this._localedata=newValue;
    }

    this._render_tree();
   }

  set data(val) {
    //Modificamos los datos de la navegación
    this._data=val;
    this._render_primary();
  }

  _onclick(event)  {
    let el=event.composedPath()[0];
    if (el.className==="navigation-principal-element") {
      let el=event.composedPath()[0];
      let arraypos=el.getAttribute("position");
      let elemento=this._data[arraypos];
      console.log(elemento);
      if (elemento.elements.length>0) {
        this._render_secondary(arraypos);
      } else {

      }
    } else if (el.className==="navigation-secundaria-element") {
      console.log("CLICK EN ELEMENTO SECUNDARIA DEL MENU");
      let  arraypos=el.getAttribute("position");
      let  link=el.getAttribute("link");
      console.log(link);
      window.location.href=link;
    }
  }

  _find_element(element, index, array) {
    console.log("--> _find_element");
    console.log(element.href);
    if (this._uri !==undefined && this._uri !== "/") {
      let uris=this._uri.split("/");
      let elements=element.href.split("/");
      //let req= /[-.\w]+@([\w-]+\.)+[\w-]{2,20}$/g;
      let req= /[-.\w]+.html/g;

      //Subelemento
      for (let i=0; i<elements.length;i++) {
        if (!req.test(uris[i]) && uris[i]===elements[i]) {
          //console.log("--- _find_element continue");
          //console.log(uris[i]);
          continue;
        /*
        } else if (!uris[i].startsWith("index.html") && uris[i]===elements[i]) {
          console.log("--- _find_element continue");
          console.log(uris[i]);
          continue;
        */
        } else if (elements[i]!=="" && uris[i]!==elements[i]) {
          //console.log("--- _find_element false");
          return false;
        } else if (req.test(elements[i]) ) {
          console.log("--- _find_element ELEMENT HTML");
          //console.log(uris[i]);
          return true;
        } else if (req.test(uris[i]) ) {
          console.log("--- _find_element URI HTML");
          //console.log(uris[i]);
          return true;
        /*
        } else if (uris[i].startsWith("index.html") ) {
          console.log("--- _find_element TRUE");
          return true;
        */
        } else if (i===elements.length-1) {
          //console.log("--- Final ");
          //console.log(uris[i]);
          //console.log(elements[i]);
          if (elements[i]==="") {
            //Buscamos en el subelemento
            return element.elements.find(this._find_element);
          }
        }
      } 
    }
  }

  

  _find_element_byid(elementid, array) {
    let actual=array;
    let res=undefined;
    array.forEach(element => {
      if (element.id==elementid) {
        res=element;
        return element;
      } else {
        if (element.elements) {
          let resk=this._find_element_byid(elementid,element.elements);
          if (resk) {
            res=resk;
            return resk;
          }
        }
      }
    });
    return res;
  }

  _render_tree() {
    let count=0;
    console.log("--> _render_tree");
    
    //Buscamos la URI
    let arbol=undefined;
    let actual=undefined;
    console.log(this._uri);
    console.log(this._startLevel);
    console.log(this._localedata);
    let locale=this._localedata;

    if (this._data===undefined) return;
    if (this._uri===undefined) this._uri="/";

    if (this._startLevel===0) {
      actual=this._data;
      console.log(actual);

      if (this._data!==undefined) {
        actual=this._data.find(function(element) {
          return element.locale===locale;
        });

        console.log(this._data_locale);
        this._data_locale=actual.elements
        actual=this._data_locale;
      }

      let div = this.shadowRoot.getElementById("navbarNavPc")
      if (div) {
        while(div.firstChild){
          div.removeChild(div.firstChild);
        }
      }
      div = this.shadowRoot.getElementById("navbarNavMovil")
      if (div) {
        while(div.firstChild){
            div.removeChild(div.firstChild);
        }
      }
    }

    console.log(actual);

    if (this._uri !==undefined && this._uri !== "/" && this._startLevel>0) {
      //Buscamos el los datos ese elemento
      actual=this._data.find(this._find_element);
      console.log(actual);
      console.log(this._uri);

      if (parseInt(actual.navTree)===parseInt(this._startLevel)-1) {
        arbol=actual.elements;
      }
      //console.log("--- BUBLE INICIO" );
      //console.log(actual);
      //console.log(actual.elements);
      while (actual && actual.elements && actual.elements.length>0) {
        let aux=actual.elements.find(this._find_element);
        if (aux!== undefined) {
          actual=aux;
          if (parseInt(actual.navTree)===parseInt(this._startLevel)-1) {
            arbol=actual.elements;
          } 
        } else {
          break;
        }
      }
      
      //Tenemos el elemento de nivel 1
    }

    if (this._startLevel>0) {
      //Estamos en un subarbol. Lo construimos a partir del actual
      if (actual===undefined) {
        console.warn("No tenemos elemento actual");
        return;
      }
    } else {
      arbol=this._data_locale;
    }
    console.log(this._data_locale);
    console.log(arbol);
    
    this.shadowRoot.getElementById("navbarNavPc").appendChild(this._render_tree_element(arbol,"navbarNavPc","pc",true));
    this.shadowRoot.getElementById("navbarNavMovil").appendChild(this._render_tree_element(arbol,"navbarNavMovil","mv",true));
    if (this._startLevel>0) {
      //Estamos en un subarbol. Lo construimos a partir del actual
      if (actual===undefined) {
        console.warn("No tenemos elemento actual");
        return;
      } else {
         this._show(actual);
      }
    }
  }

  _render_tree_element(element,divid,type,first=false) {
    let count=0;
    console.log("--> _render_tree_element");
    console.log(element);

    let ul=document.createElement('ul');
    ul.id=type+"_"+"sub_"+divid;
    element.forEach(element => {
      let elemento=document.createElement('li');
      elemento.id=element.id;//divid+"-"+count;
      elemento.className="navigation-element";
      elemento.setAttribute("position",count)
      elemento.setAttribute("level",element.navTree);
      
      let flecha=document.createElement('i');
      flecha.className="fas fa-sort-down";
      elemento.innerText=element.navText;
      elemento.appendChild(flecha);
      
      if (element.navTree<=this._levels) {
        if (element.elements && element.elements.length>0) {
          let el=this._render_tree_element(element.elements,elemento.id,type);
          if (type=="mv") {
            elemento.appendChild(el);
          } else {
            if (divid=="navbarNavPc") {
              //this.shadowRoot.getElementById("navbarNavPc").appendChild(el);
              let div=this.shadowRoot.getElementById("navbarNavPc").parentElement;
              div.appendChild(el);
            }
          }
        } else {
          elemento.setAttribute("link",element.href);
        }
      }
      if (parseInt(element.navTree)>parseInt(this._startLevel)) {
        ul.style.display="none";
      }
      //if (first) ul.classList.add("navbar-nav");
      ul.appendChild(elemento);
      count++;
    });

    this.shadowRoot.querySelector(".menu_movil").style.display="none";
    return ul;
  }

  _find(funcion) {
    console.log("--> _find");
    console.log(element);
    let actual=this._data;
    while (actual && actual.elements && actual.elements.length>0) {
      let aux=actual.elements.find(funcion);
      if (aux!== undefined) {
        actual=aux;
      } else {
        break;
      }
    }
    return actual;
  }

  _find_tree(elemento, elementos, arrayres=undefined)  {
    //Devolvera el id del elemento desde lo mas profundo a lo más arriba del árbol
    if (arrayres===undefined) {
      arrayres=[];
    }
    let res=undefined;
    elementos.forEach(element => {
      if (element.id==elemento.id) {
        arrayres.push(element.id);
        res=arrayres;
        return res;
      } else {
        if (element.elements) {
          let resk=this._find_tree(elemento,element.elements,arrayres);
          if (resk && resk.length>0) {
            arrayres.push(element.id);
            res=arrayres;
            return res;
          }
        }
      }
    });
    return res;
  }

  _hideall() {
    console.log("--> _hideall");
    let elhtml=this.shadowRoot.querySelectorAll('ul:not([style*="display: none"])');
    console.log(elhtml);
    elhtml.forEach(element => {
      element.style.display="none";
    });
  }

  _show(element) {
    //Debe mostrar el arbol desde el padre hasta el hijo
    console.log("--> show");
    console.log(element);

    if (this._onlyone) {
      this._hideall();
    }

    let navtree=this._find_tree(element,this._data);
    console.log(navtree);

    navtree.forEach(elementid => {
      console.log(elementid);
      let elhtml=this.shadowRoot.getElementById(elementid);
      if (elhtml) {
        let ulChildren = elhtml.children;                
          let childrenLength = ulChildren.length;
          let ul=undefined;
          for(let i = 0; i < childrenLength; i++){
              if(ulChildren[i].nodeName.toLowerCase() === 'ul'){
                  ul= ulChildren[i];
              }
          }
          if (ul) {
            if (ul.style.display==="none") {
              ul.style.display="";
            } else {
              ul.style.display="none";
            }
          }
      }
    });

    /*
    let actual=this._data.find(this._find_element);

    let actual_el=this.shadowRoot.getElementById(actual.id);
    if (actual_el) {
      actual_el.style.display="";
    }
    while (actual && actual.elements && actual.elements.length>0) {
      let aux=actual.elements.find(this._find_element);
      console.log(aux);
      if (aux!== undefined) {
        console.log(aux);
        //aux.style.display="";
        actual=aux;
        actual_el=this.shadowRoot.getElementById(actual.id);
        if (actual_el) {
          let ulChildren = actual_el.children;                
          let childrenLength = ulChildren.length;
          let ul=undefined;
          for(let i = 0; i < childrenLength; i++){
              if(ulChildren[i].nodeName.toLowerCase() === 'ul'){
                  ul= ulChildren[i];
              }
          }
          console.log(ul);
          if (ul) {
            if (ul.style.display==="none") {
              ul.style.display="";
            } else {
              ul.style.display="none";
            }
          }
        }
      } else {
        break;
      }
    }
    */
  }
  
}
customElements.define(UVaNavegacion.is, UVaNavegacion);

class UVaNavegacionHorizontal extends UVaNavegacion {
  static get is() {
    return 'uva-navegacion-horizontal';
  }
  constructor(...args) {
    // Constructor
    try {
      const self = super(...args);
    } catch(e) {
	  // Si no somos capaces de cargar al padre, error
      throw "Error al crear el elemento UVaNavegacionHorizontal " + e;
    } finally {
      this.shadowRoot.innerHTML = `
      <style>
      //@import url('https://stackpath.bootstrapcdn.com/bootstrap/4.1.3/css/bootstrap.min.css');
      //@import url('https://use.fontawesome.com/releases/v5.5.0/css/all.css');
      :host {
        all: initial;
      }
      @media all and (max-width: 600px) {
        .toggler {
          display: visible;
        }
        .menu_pc {
          display: none;
        }
        li {
          list-style: none;
        }
      }
      @media all and (min-width: 600px) {
        .toggler {
            display: none;
        }
        .menu_pc {
          display: visible;
        }
        ul {
          display: block;
          clear: both;
        }
        li {
          list-style: none;
          float: left;
        }
      }
      ul {
        padding-inline-start: 0px;
      }
      li {
        list-style: none;
      }
      nav{
          display: block;
          clear: both;
      }
      </style>
      <nav id="nav">
        <button class="toggler" type="button" aria-controls="navbar" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon">Menu</span>
        </button>
        <div class="menu_pc" id="navbarNavPc"></div>
        <div class="menu_movil" id="navbarNavMovil"></div>
      </nav>
      `;
    }
  }

  set data(val) {
    //Modificamos los datos de la navegación
    this._data=val;
    this._render_tree();
  }

  _onclick(event)  {
    super._onclick(event);
    console.log("--> ON CLICK NAV HORIZONTAL");

    let el=event.composedPath()[0];
    console.log(el);
    if (el.className==="navigation-element") {
      let el=event.composedPath()[0];
      console.log("CLICK EN ELEMENTO DEL MENU");
      console.log(el);
      let elid=el.getAttribute("id");
      let navlevel=el.getAttribute("level");
      let arraypos=el.getAttribute("position");
      let ellink=el.getAttribute("link");

      let element=this._find_element_byid(elid,this._data);
      console.log("--- ELEMENTO CLICK");
      console.log(element);
      let elhtmlid=element.id;
      let elhtml= this.shadowRoot.getElementById(element.id);
      console.log(elhtml);

      

      console.log(element.elements);
      if (element && element.elements && element.elements.length>0) {
        if (element.navTree<=this._levels) {
          //mostramos el elemento
          //this._show(element);
          console.log("SHOW ELEMENT");

          let elnav=this.shadowRoot.getElementById("nav");
          elnav.childNodes.forEach(element => {
            console.log(element);
            console.log(element.nodeName);
            if (element.nodeName=="UL") {
              if (element.style && element.style.display) {
                element.style.display="none";
                console.log(element.style.display);
              }
            }
            console.log(element);
          });

          let el=this.shadowRoot.getElementById("pc"+"_sub_"+element.id);
          if (el.style.display=="none") {
            el.style.display="";
          } else {
            el.style.display="none";
          }
          el=this.shadowRoot.getElementById("mv"+"_sub_"+element.id);
          console.log(el);
          if (el.style.display=="none") {
            el.style.display="";
          } else {
            el.style.display="none";
          }
          


        } else {
          //Le llevamos a la página
          console.log("GO LINK");
          console.log("element.link");
        }
      } else {
        let  link=el.getAttribute("link");
        console.log(link);
        window.location.href=link;
      }
    } else if (el.className==="navbar-toggler-icon") {
      console.log("CLICK EN BOTON TOGGLE");
      console.log(el);
      let menu=this.shadowRoot.querySelector(".menu_movil");
      console.log(menu);
      console.log(menu.style);
      console.log(menu.style.display);
      if (menu.style && menu.style.display && menu.style.display=="none") {
        console.log("CAMBIO A VISIBLE");;
        menu.style.display="";
      } else {
        menu.style.display="none";
      }
      console.log(menu);
    }
  }
}
