'use strict';


class UVaSlideshow extends HTMLElement {

  static get is() {
    return 'uva-slideshow';
  }
  
  constructor(...args) {
    // Constructor
    try {
      const self = super(...args);
      console.log("-->Slideshow constructor");
      //this.shadow=undefined;
      //VAriables
      this._indice = 0;
      this._childs = 0;
      this._objects = [];
      this._dots = [];
      this._auto=false;
      this._timer = undefined;
      this._options= {
        slideIndex: 0,
        timming: 2000,
        automated: true,
        num_show: 3,
        dots: true,
      };
      //Funciones
      this._show = this._show.bind(this);
      this._next = this._next.bind(this);
      this._onclick = this._onclick.bind(this);
      this._render_element = this._render_element.bind(this);
      this._dot_click = this._dot_click.bind(this);
      //Shadow
      let shadowRoot = this.attachShadow({mode: 'open'});
      this.shadowRoot.innerHTML = `
      <style>
      :host {
        overflow: hidden;
        text-align: center; 
      }
      @media screen and (max-width: 1199px) {
         height: 380px;
      }
      
      @media screen and (max-width: 999px) {
        height: 300px;
      }
      
      @media screen and (max-width: 769px) {
        height: 200px;
      }
      
      @media screen and (max-width: 400px) {
        height: 180px;
      }
      </style>
     
      `;
    } catch(err) {
      console.error(err);
      throw 'Ha habido un error al crear los elementos de Mi Portal';
    } finally {
    }
  }

  connectedCallback() {
    console.log("-->Slideshow connectedCallback");
    //Variables
    if (this.getAttribute('auto')) {
      this._auto=this.getAttribute('auto');
    };
    //Funciones 
    this.addEventListener('click', this._onclick);
    this.addEventListener('slideshowimg_render',this._render_element);
    this.addEventListener('slideshowdot_click',this._dot_click);
    this._loadChilds();
    this._render();
  }

  disconnectedCallback() {
    console.log("---Slideshow disconnectedCallback");
    this.removeEventListener('click', this._onclick);
    this.removeEventListener('slideshowimg_render',this._render_element);
    this.removeEventListener('slideshowdot_click',this._dot_click);
  }

  adoptedCallback() {
    console.log("---Slideshow adoptedCallback");
  }
  
  static get observedAttributes () {
    return ['slotchange'];
 }

  attributeChangedCallback(name, oldValue, newValue) {
    console.log("---Slideshow attributeChangedCallback");
    console.log(name);
    console.log(oldValue);
    console.log(newValue);
  }

  addElement(val) {
    console.log("---Slideshow addElement");
    this._objects.push(val);
  }

  set indice(val) {
    this._indice = val;
  }

  get indice() {
    return this._indice;
  }

  _render() {
    console.log("---Slideshow _render ","auto ",this._auto);
    console.log("---Slideshow _render ",this._objects);
    
  }

  _render_element(event) {
    let el=Object.assign(event.detail.data);
    //console.log("---Slideshow _render_element ",el," Detail ",event.detail, " object ", this._objects);
    this._objects.push(el);
    //console.log("---Slideshow _render_element ",this._objects.length, " ", this._childs);
    //SI tenemos todos los elementos
    if (this._objects.length === this._childs)  {
      if (this._auto) {
        this._show(this._indice);
      }
      if (this._options.dots) {
        this._objects.forEach(element => {
          console.log(element);
          let dot = new UVaSlideshowDot();
          dot.data=element;
          this._dots.push(dot);
          this.shadowRoot.appendChild(dot);
        });
      }
    }
  }

  _dot_click(event) {
    //Al hacer click desactivamos el automated
    this._endtimer();
    this._options.automated=false;
    let el=Object.assign(event.detail.data);
    //console.log("---Slideshow _dot_click ",el," Detail ",event.detail, " object ", this._dots);
    let indice
    this._dots.forEach((element, index, array) => {
      if (element === el) {
        this._show(index);
      }
    });
  }

  _loadChilds() {
    console.log("---Slideshow _loadChilds");
    this._options.num_show=1;
    let childs=this.childNodes;
    this.childNodes.forEach(element => {
      if (element.nodeName==="UVA-SLIDESHOW-IMG") {
        this.shadowRoot.appendChild(Object.assign(element));
        //this._objects.push(element);
        this._childs++;
      }
    });
  }

  _show(indice) { 
    this._indice=indice;
    //console.log("--- Slideshow _show ",indice);
    //Ocultamos todos los elementos
    if (this._objects && this._objects.length > 0) {
      this._objects.forEach(el => {
        el.style.display = "none";
      });
    }
    //Ocultamos todos los dots
    if (this._dots && this._dots.length > 0) {
      this._dots.forEach(el => {
        el.classList.remove("active");
      });
    }
    //Mostramos el elegido
    if (this._objects && this._objects.length >0  && this._objects.length >= indice) {
      let el=this._objects[indice];
      el.style.display = "block";
    }
    //Activamos _dots dot
    if (this._dots && this._dots.length >0 && this._dots.length >= indice) {
      let el=this._dots[indice];
      el.classList.add("active");
    }
    //Activamos el timer
    if (this._options.automated) {
      this._starttimer();
    }
  }

  _starttimer() {
    this._endtimer();
    if (this._options.automated) {
      this._timer = setTimeout(this._next, this._options.timming, "nameOfSlideshow", this._options);
    }
  }

  _endtimer() {
    clearTimeout(this._timer);
  }

  _next() { //=this.options.slideIndex
    this._indice++;
    if (this._indice >= this._objects.length) {
      this._indice=0;
    }
    this._show(this._indice);
  }

  
  _refresh() {
    let event=new CustomEvent("next");
    this.dispatchEvent(event);
  }

  doclick() {
    console.log("doclick");
    console.log(this);
  }

  _onclick(event)  {
    console.log("ON CLICK");
  }

  showSlide(nameOfSlideshow, options) {
  }
  
}
customElements.define(UVaSlideshow.is, UVaSlideshow);

class UVaSlideshowDot extends HTMLElement {

  static get is() {
    return 'uva-slideshow-dot';
  }
  
  constructor(...args) {
    // Constructor
    try {
      const self = super(...args);
      //VAriables
      this._data = undefined;
      //Funciones
      this._onclick = this._onclick.bind(this);
      //Shadow
      let shadowRoot = this.attachShadow({mode: 'open'});
      this.shadowRoot.innerHTML = `
        <style>
        :host {
          margin: 0 0.2em;
          width: 25px;
          height: 5px;
          border: 1px solid black;
          background-color: white;
          display: inline-block; 
        }
        :host .active {
          background-color: black;
          font-weight: bold; 
        }
        :hover {
          cursor: pointer; 
        }
        .active {
          background-color: black;
          font-weight: bold; 
        }
        </style>
        <span id="content"  />
        `;
    } catch(err) {
      console.error(err);
      throw 'Ha habido un error al crear los elementos de Mi Portal';
    } finally {
    }
  }

  connectedCallback() {
    //Variables
    //Funciones
    this.addEventListener('click', this._onclick);
    //Contenido
    //console.log("--- UVaSlideshowImg src ",this._src," alt ",this._alt," title ",this._title);
    this._render();
  }

  disconnectedCallback() {
    this.removeEventListener('click', this._onclick);
  }

  adoptedCallback() {
    console.log("---UVaSlideshowImg adoptedCallback");
  }
  
  static get observedAttributes () {
  }

  attributeChangedCallback(name, oldValue, newValue) {
    console.log("---UVaSlideshowImg attributeChangedCallback");
    console.log(name);
    console.log(oldValue);
    console.log(newValue);
  }

  get data() {
    return this._data;
  }
  
  set data(val) {
    this._data=val;
  }

  _render() {
  }

  _onclick() {
    console.log("---UVaSlideshowDot _onclick");
    let ev=new CustomEvent("slideshowdot_click",{
      bubbles: true,
      cancelable: false,
      composed: true,
      detail: { data: this}
    });
    this.dispatchEvent(ev);
  }
  
}
customElements.define(UVaSlideshowDot.is, UVaSlideshowDot);

class UVaSlideshowImg extends HTMLElement {

  static get is() {
    return 'uva-slideshow-img';
  }
  
  constructor(...args) {
    // Constructor
    try {
      const self = super(...args);
      //VAriables
      this._src = undefined;
      this._alt = undefined;
      this._title = undefined;
      //Funciones
      this._onclick = this._onclick.bind(this);
      //Shadow
      let shadowRoot = this.attachShadow({mode: 'open'});
      this.shadowRoot.innerHTML = `
        <style>
        </style>
        <img id="content" />
        `;
    } catch(err) {
      console.error(err);
      throw 'Ha habido un error al crear los elementos de Mi Portal';
    } finally {
    }
  }

  connectedCallback() {
    //Variables
    this._src = this.getAttribute('src');
    this._alt = this.getAttribute('alt');
    this._title = this.getAttribute('title');
    //Funciones
    this.addEventListener('click', this._onclick);
    //Contenido
    //console.log("--- UVaSlideshowImg src ",this._src," alt ",this._alt," title ",this._title);
    this._render();
  }

  disconnectedCallback() {
    this.removeEventListener('click', this._onclick);
  }

  adoptedCallback() {
    console.log("---UVaSlideshowImg adoptedCallback");
  }
  
  static get observedAttributes () {
  }

  attributeChangedCallback(name, oldValue, newValue) {
    console.log("---UVaSlideshowImg attributeChangedCallback");
    console.log(name);
    console.log(oldValue);
    console.log(newValue);
  }

  _render() {
    //console.log("---UVaSlideshowImg _render");
    let el=this.shadowRoot.getElementById("content");
    el.setAttribute("src",this._src);
    el.setAttribute("title",this._title);
    el.setAttribute("alt",this._alt);
    this.style.display="none";

    let ev=new CustomEvent("slideshowimg_render",{
      bubbles: true,
      cancelable: false,
      composed: true,
      detail: { data: this}
    });
    this.dispatchEvent(ev);
  }

  _onclick() {
    console.log("---SlideshowImg _onclick");
  }
  
}
customElements.define(UVaSlideshowImg.is, UVaSlideshowImg);


